'use client'

import React, { useState, useEffect, useCallback } from "react";
import { useRouter } from "next/navigation";
import toast from 'react-hot-toast';
import { getUserId, getToken, isAuthenticated } from "@/lib/utils/auth";
import { useSocket } from "@/contexts/SocketContext";
import { useNotification, requestNotificationPermission } from "@/contexts/NotificationContext";
import { getMyConversations, getMessages, markConversationAsRead, createChatMessage } from "@/lib/api/messageApi";
import { SidebarComponent } from "@/components/Sidebar/SidebarComponent";
import ConversationList from "@/components/Message/ConversationList";
import ChatArea from "@/components/Message/ChatArea";
import ConnectionStatus from "@/components/Message/ConnectionStatus";
import { Conversation, Message } from "@/types/api.types";

export default function MessagePage() {
    const [conversations, setConversations] = useState<Conversation[]>([]);
    const [selectedChat, setSelectedChat] = useState<Conversation | null>(null);
    const [messages, setMessages] = useState<Message[]>([]);
    const [currentUserId, setCurrentUserId] = useState<string | null>(null);
    const [loadingMessages, setLoadingMessages] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [isMobile, setIsMobile] = useState(false);
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const [unreadByConversation, setUnreadByConversation] = useState<{ [key: string]: number }>({});

    const router = useRouter();
    const { sendMessage, isConnected, registerMessageCallbacks } = useSocket();
    const { addNotification } = useNotification();

    // Check authentication
    useEffect(() => {
        if (typeof window === 'undefined') return;
        if (!isAuthenticated()) {
            router.push('/login');
        }
    }, [router]);

    // Check mobile
    useEffect(() => {
        if (typeof window === 'undefined') return;
        const checkMobile = () => {
            setIsMobile(window.innerWidth < 768);
        };
        checkMobile();
        window.addEventListener('resize', checkMobile);
        return () => window.removeEventListener('resize', checkMobile);
    }, []);

    // Request notification permission
    useEffect(() => {
        requestNotificationPermission();
    }, []);

    // Init user ID
    useEffect(() => {
        const userId = getUserId();
        setCurrentUserId(userId);
    }, []);

    // Fetch conversations
    const fetchConversations = useCallback(async () => {
        try {
            setIsLoading(true);
            const data = await getMyConversations();
            const sortedConvs = (data || []).sort((a: Conversation, b: Conversation) => {
                const timeA = a.lastMessageTime ? new Date(a.lastMessageTime).getTime() : 0;
                const timeB = b.lastMessageTime ? new Date(b.lastMessageTime).getTime() : 0;
                return timeB - timeA;
            });
            setConversations(sortedConvs);
        } catch (err) {
            console.error('Error fetching conversations:', err);
            toast.error('Không thể tải danh sách trò chuyện');
        } finally {
            setIsLoading(false);
        }
    }, []);

    useEffect(() => {
        const token = getToken();
        if (token) {
            fetchConversations();
        }
    }, [fetchConversations]);

    // Fetch messages when chat is selected
    const fetchMessages = useCallback(async (conversationId: string) => {
        try {
            setLoadingMessages(true);
            const data = await getMessages(conversationId, 0, 50);
            const msgs = data?.content || [];
            setMessages(msgs.reverse());

            // Mark as read
            await markConversationAsRead(conversationId);

            // Clear unread for this conversation
            setUnreadByConversation(prev => ({
                ...prev,
                [conversationId]: 0
            }));
        } catch (err) {
            console.error('Error fetching messages:', err);
        } finally {
            setLoadingMessages(false);
        }
    }, []);

    // Handle chat selection
    const handleSelectChat = (conversation: Conversation) => {
        setSelectedChat(conversation);
        if (conversation.id) {
            fetchMessages(conversation.id);
        }
    };

    // Register socket callbacks
    useEffect(() => {
        if (!registerMessageCallbacks) return;

        registerMessageCallbacks({
            onMessageReceived: (data: unknown) => {
                const message = data as Message;
                if (selectedChat && message.conversationId === selectedChat.id) {
                    setMessages(prev => [...prev, message]);
                } else {
                    // Update unread count
                    setUnreadByConversation(prev => ({
                        ...prev,
                        [message.conversationId || '']: (prev[message.conversationId || ''] || 0) + 1
                    }));
                }
                // Refresh conversations to update last message
                fetchConversations();
            },
            onMessageSent: (data: unknown) => {
                const message = data as Message;
                setMessages(prev => [...prev, message]);
            },
        });
    }, [registerMessageCallbacks, selectedChat, fetchConversations]);

    // Handle send message
    const handleSendMessage = async (messageText: string) => {
        if (!messageText.trim() || !selectedChat?.id) return;

        try {
            // Send via socket first
            const sent = sendMessage({
                conversationId: selectedChat.id,
                message: messageText,
            });

            if (!sent) {
                // Fallback to API
                await createChatMessage(selectedChat.id, messageText);
                fetchMessages(selectedChat.id);
            }
        } catch (err) {
            console.error('Error sending message:', err);
            toast.error('Không thể gửi tin nhắn');
        }
    };

    return (
        <div className="flex min-h-screen">
            {/* Mobile menu button */}
            {isMobile && (
                <button
                    onClick={() => setSidebarOpen(!sidebarOpen)}
                    className="fixed top-4 left-4 z-50 p-2 bg-white rounded-md shadow-md border md:hidden"
                    aria-label="Toggle menu"
                >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        {sidebarOpen ? (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        ) : (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                        )}
                    </svg>
                </button>
            )}

            {/* Mobile overlay */}
            {isMobile && sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar - Desktop */}
            <div className="hidden md:block w-[240px] h-screen bg-white border-r flex-shrink-0">
                <SidebarComponent />
            </div>

            {/* Sidebar - Mobile */}
            <div
                className={`
                    md:hidden fixed top-0 left-0 w-64 h-screen bg-white border-r z-50
                    transition-transform duration-300 ease-in-out
                    ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
                `}
            >
                <SidebarComponent />
            </div>

            {/* Main Content */}
            <div className={`flex-1 min-h-screen w-full max-w-full overflow-hidden ${isMobile ? 'pt-16' : ''}`}>
                <div className="flex h-screen">
                    {/* Conversation List - Using Component */}
                    <div className="w-80 bg-white border-r flex flex-col">
                        <div className="p-4 border-b">
                            <h2 className="text-xl font-bold text-gray-800">Messages</h2>
                            <div className="mt-2">
                                <ConnectionStatus isConnected={isConnected} />
                            </div>
                        </div>

                        <ConversationList
                            conversations={conversations}
                            selectedChat={selectedChat}
                            onSelectChat={handleSelectChat}
                            isLoading={isLoading}
                            unreadByConversation={unreadByConversation}
                        />
                    </div>

                    {/* Chat Area - Using Component */}
                    <ChatArea
                        conversation={selectedChat}
                        messages={messages}
                        currentUserId={currentUserId}
                        onSendMessage={handleSendMessage}
                        isConnected={isConnected}
                        loadingMessages={loadingMessages}
                    />
                </div>
            </div>
        </div>
    );
}
