'use client'

import React, { useState, useRef, useEffect } from 'react';
import { Send, Image, Smile } from 'lucide-react';
import { Conversation, Message } from '@/types/api.types';
import MessageBubble from './MessageBubble';

interface ChatAreaProps {
    conversation: Conversation | null;
    messages: Message[];
    currentUserId: string | null;
    onSendMessage: (message: string) => void;
    isConnected?: boolean;
    loadingMessages?: boolean;
}

const ChatArea: React.FC<ChatAreaProps> = ({
    conversation,
    messages,
    currentUserId,
    onSendMessage,
    isConnected = false,
    loadingMessages = false,
}) => {
    const [input, setInput] = useState('');
    const messagesEndRef = useRef<HTMLDivElement>(null);

    // Scroll to bottom when messages change
    useEffect(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, [messages]);

    const handleSend = () => {
        if (!input.trim()) return;
        onSendMessage(input.trim());
        setInput('');
    };

    const handleKeyPress = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            handleSend();
        }
    };

    // Empty state when no conversation selected
    if (!conversation) {
        return (
            <div className="flex-1 flex items-center justify-center bg-gradient-to-b from-gray-50 to-white">
                <div className="text-center">
                    <div className="w-24 h-24 mx-auto mb-6 rounded-full bg-gradient-to-br from-sky-100 to-blue-100 flex items-center justify-center">
                        <svg className="w-12 h-12 text-sky-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
                                d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                        </svg>
                    </div>
                    <h3 className="text-xl font-bold text-gray-800 mb-2">Select a conversation</h3>
                    <p className="text-gray-500 text-sm">Choose a chat to start messaging</p>
                </div>
            </div>
        );
    }

    return (
        <div className="flex-1 flex flex-col bg-gray-50">
            {/* Chat Header */}
            <div className="bg-white p-4 border-b flex items-center gap-3 shadow-sm">
                <div className="relative">
                    <img
                        src={conversation.conversationAvatar || "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_640.png"}
                        alt="Avatar"
                        className="w-10 h-10 rounded-full object-cover border-2 border-white shadow-sm"
                    />
                    {isConnected && (
                        <div className="absolute bottom-0 right-0 w-3 h-3 bg-green-500 rounded-full border-2 border-white"></div>
                    )}
                </div>
                <div className="flex-1">
                    <p className="font-semibold text-gray-800">
                        {conversation.conversationName || 'Conversation'}
                    </p>
                    <p className="text-xs text-gray-500">
                        {isConnected ? 'Online' : 'Offline'}
                    </p>
                </div>
            </div>

            {/* Messages Area */}
            <div className="flex-1 overflow-y-auto p-4 space-y-3">
                {loadingMessages ? (
                    <div className="flex justify-center items-center h-full">
                        <div className="text-center">
                            <div className="animate-spin w-8 h-8 border-4 border-sky-500 border-t-transparent rounded-full mx-auto mb-3"></div>
                            <p className="text-gray-500 text-sm">Loading messages...</p>
                        </div>
                    </div>
                ) : messages.length > 0 ? (
                    messages.map((msg) => (
                        <MessageBubble
                            key={msg.id}
                            message={msg}
                            isOwn={msg.senderId === currentUserId}
                        />
                    ))
                ) : (
                    <div className="flex justify-center items-center h-full">
                        <div className="text-center">
                            <p className="text-gray-500">No messages yet</p>
                            <p className="text-gray-400 text-sm">Start the conversation!</p>
                        </div>
                    </div>
                )}
                <div ref={messagesEndRef} />
            </div>

            {/* Message Input */}
            <div className="bg-white p-4 border-t">
                <div className="flex items-center gap-3">
                    {/* Attachment button */}
                    <button className="p-2 hover:bg-gray-100 rounded-full transition-colors text-gray-500 hover:text-gray-700">
                        <Image size={20} />
                    </button>

                    {/* Input */}
                    <div className="flex-1 relative">
                        <input
                            type="text"
                            placeholder="Type a message..."
                            value={input}
                            onChange={(e) => setInput(e.target.value)}
                            onKeyPress={handleKeyPress}
                            className="w-full px-4 py-3 pr-12 border border-gray-200 rounded-full focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent transition-all bg-gray-50"
                        />
                        <button className="absolute right-3 top-1/2 -translate-y-1/2 p-1.5 hover:bg-gray-100 rounded-full transition-colors text-gray-500 hover:text-gray-700">
                            <Smile size={18} />
                        </button>
                    </div>

                    {/* Send button */}
                    <button
                        onClick={handleSend}
                        disabled={!input.trim()}
                        className="p-3 bg-gradient-to-r from-sky-500 to-blue-500 text-white rounded-full hover:from-sky-600 hover:to-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all shadow-md hover:shadow-lg"
                    >
                        <Send size={20} />
                    </button>
                </div>
            </div>
        </div>
    );
};

export default ChatArea;
