'use client'

import React, { useMemo } from 'react';
import { MessageCircle } from 'lucide-react';
import { Conversation } from '@/types/api.types';

interface ConversationListProps {
    conversations: Conversation[];
    selectedChat: Conversation | null;
    onSelectChat: (conversation: Conversation) => void;
    isLoading?: boolean;
    unreadByConversation?: { [key: string]: number };
}

interface ConversationItemProps {
    conv: Conversation;
    isSelected: boolean;
    unreadCount: number;
    onClick: () => void;
}

const ConversationItem: React.FC<ConversationItemProps> = ({
    conv,
    isSelected,
    unreadCount,
    onClick,
}) => {
    const formatTime = (dateString?: string) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        const now = new Date();
        const diff = Math.floor((now.getTime() - date.getTime()) / 1000);

        if (diff < 60) return 'Now';
        if (diff < 3600) return `${Math.floor(diff / 60)}m`;
        if (diff < 86400) return `${Math.floor(diff / 3600)}h`;
        return date.toLocaleDateString();
    };

    return (
        <div
            onClick={onClick}
            className={`flex items-center gap-3 p-4 cursor-pointer hover:bg-gray-50 transition-all duration-200 ${isSelected
                    ? 'bg-gradient-to-r from-sky-50 to-blue-50 border-r-4 border-sky-500'
                    : ''
                } ${unreadCount > 0 ? 'bg-sky-50/50' : ''}`}
        >
            {/* Avatar */}
            <div className="relative flex-shrink-0">
                <img
                    src={
                        conv.conversationAvatar ||
                        'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_640.png'
                    }
                    alt="Avatar"
                    className="w-12 h-12 rounded-full object-cover border-2 border-white shadow-sm"
                />
                {unreadCount > 0 && (
                    <div className="absolute -top-1 -right-1 min-w-[20px] h-5 flex items-center justify-center bg-gradient-to-r from-red-500 to-pink-500 text-white text-xs font-bold rounded-full px-1">
                        {unreadCount > 99 ? '99+' : unreadCount}
                    </div>
                )}
            </div>

            {/* Content */}
            <div className="flex-1 min-w-0">
                <div className="flex items-center justify-between mb-1">
                    <p className={`font-semibold text-gray-800 truncate ${unreadCount > 0 ? 'text-sky-800' : ''}`}>
                        {conv.conversationName || 'Conversation'}
                    </p>
                    <span className="text-xs text-gray-400 flex-shrink-0 ml-2">
                        {formatTime(conv.lastMessageTime)}
                    </span>
                </div>
                <p className={`text-sm truncate ${unreadCount > 0 ? 'text-gray-700 font-medium' : 'text-gray-500'}`}>
                    {conv.lastMessage || 'No messages yet'}
                </p>
            </div>
        </div>
    );
};

const ConversationList: React.FC<ConversationListProps> = ({
    conversations,
    selectedChat,
    onSelectChat,
    isLoading = false,
    unreadByConversation = {},
}) => {
    // Sort conversations by unread first, then by time
    const sortedConversations = useMemo(() => {
        return [...conversations].sort((a, b) => {
            const unreadA = unreadByConversation[a.id] || 0;
            const unreadB = unreadByConversation[b.id] || 0;

            // Unread first
            if (unreadA > 0 && unreadB === 0) return -1;
            if (unreadB > 0 && unreadA === 0) return 1;

            // Then by time
            const timeA = a.lastMessageTime ? new Date(a.lastMessageTime).getTime() : 0;
            const timeB = b.lastMessageTime ? new Date(b.lastMessageTime).getTime() : 0;
            return timeB - timeA;
        });
    }, [conversations, unreadByConversation]);

    if (isLoading) {
        return (
            <div className="flex-1 overflow-y-auto">
                {Array.from({ length: 5 }).map((_, i) => (
                    <div key={`skeleton-${i}`} className="flex items-center gap-3 p-4 animate-pulse">
                        <div className="w-12 h-12 bg-gray-200 rounded-full flex-shrink-0" />
                        <div className="flex-1">
                            <div className="h-4 bg-gray-200 rounded w-32 mb-2" />
                            <div className="h-3 bg-gray-100 rounded w-48" />
                        </div>
                    </div>
                ))}
            </div>
        );
    }

    if (sortedConversations.length === 0) {
        return (
            <div className="flex-1 flex flex-col items-center justify-center p-6 text-center">
                <div className="w-16 h-16 mb-4 rounded-full bg-gradient-to-br from-sky-100 to-blue-100 flex items-center justify-center">
                    <MessageCircle className="w-8 h-8 text-sky-400" />
                </div>
                <h3 className="font-semibold text-gray-800 mb-1">No conversations yet</h3>
                <p className="text-sm text-gray-500">Start a conversation with someone!</p>
            </div>
        );
    }

    return (
        <div className="flex-1 overflow-y-auto">
            {sortedConversations.map((conv) => (
                <ConversationItem
                    key={conv.id}
                    conv={conv}
                    isSelected={selectedChat?.id === conv.id}
                    unreadCount={unreadByConversation[conv.id] || 0}
                    onClick={() => onSelectChat(conv)}
                />
            ))}
        </div>
    );
};

export default ConversationList;
