'use client'

import React from 'react';
import { Message } from '@/types/api.types';

interface MessageBubbleProps {
    message: Message;
    isOwn: boolean;
}

const MessageBubble: React.FC<MessageBubbleProps> = ({ message, isOwn }) => {
    const formatTime = (dateString?: string) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    };

    const messageContent = message.message || message.content || '';

    return (
        <div className={`flex ${isOwn ? 'justify-end' : 'justify-start'}`}>
            <div
                className={`max-w-[70%] px-4 py-2.5 rounded-2xl ${isOwn
                        ? 'bg-gradient-to-r from-sky-500 to-blue-500 text-white rounded-br-sm'
                        : 'bg-white text-gray-800 shadow-sm border border-gray-100 rounded-bl-sm'
                    }`}
            >
                <p className="text-sm leading-relaxed whitespace-pre-wrap break-words">
                    {messageContent}
                </p>
                <p className={`text-xs mt-1.5 ${isOwn ? 'text-white/70' : 'text-gray-400'}`}>
                    {formatTime(message.createdDate || message.createdAt)}
                    {isOwn && message.read && (
                        <span className="ml-2">✓✓</span>
                    )}
                </p>
            </div>
        </div>
    );
};

export default MessageBubble;
